.skipsNavOpen { display: table-cell; width: 2em; padding: 0; text-align: center; vertical-align: middle; background-color: #3040cc; color: white; }
.skipsNavVeg { font-size: 2em; }

.skipsSideNav {
	display: grid;
	grid-template-columns: 100px 1fr;
	grid-template-rows: 50px 50px 50px 50px 50px;

	height: 100%;
	width: 0;
	position: fixed;
	z-index: 10;
	top: 0;
	left: 0;
	padding-top: 60px;
	text-align: left;
	background-color: #3040cc;
	color: white;
	transition: 0.3s;
}
.skipsSideNav td {
	font-size: 1.2em;
	padding: 5px 5px 2px 2px;
}
.skipsSideNav input {
	min-width: 200px;
}
.skipsSideNav button {
	height: 50px;
	width: 208px;
	background-color: #aaaa40;
}
.navClose { /* link to close side panel */
	position: absolute;
	top: 0;
	right: 15px;
	font-size: 2em;
	text-decoration: none;
	color: white;
	transition: 0.1s;
}
