.skipsDefault {
	margin: 0;
	font-family: "Lato", sans-serif;
}

#skipsPageWrapper   { width: 100%; max-width: 832px; }
#skipsHeaderWrapper { display: table; width: 100%; }
#skipsNavOpen       { display: table-cell; width: 2em; padding: 0; text-align: center; vertical-align: middle; background-color: #3040cc; color: white; }
#skipsApiTitle      { font-size: 1.5em; white-space: nowrap; background-color: #3040cc; color: white; text-shadow: #03040c 0px 2px 2px; }
#skipsApiError      { color: red; padding: 0; background-color: gold; }
#skipsApiInfo       { font-size: 0.9em; white-space: nowrap; color: #3040cc; }
#skipsApiInfo ul    { margin: 0; padding: 0; }
#skipsApiInfo ul li { padding-right: 1.5em; display: inline-block; color: darkblue; }
#skipsDesktopPanel  { display: inline-block; white-space: nowrap; }
.skipsSelect        { font-size: 1.1em; background-color: #B0C040; color: white; }
.skipsSelect select { border: none; border-radius: 3px; color: DarkBlue; }

.skipsGridWrapper { /*!!?? not currently used */
	display: grid;
	grid-template-columns: 100px 1fr;
	/* grid-gap: 10px; */
}
.skipsGridCell { /*!!?? not currently used */
	background-color: #444;
	color: #fff;
	border-radius: 5px;
}

.skipsDataTable table {
	width: auto;
	border-collapse: collapse;
}
.skipsDataTable td {
	font-size: 0.8em;
	border: 1px solid #B0C040;
	padding: 3px 7px 2px 7px;
}
/* .skipsDataTable tr:nth-child(even) { */
.skipsDataTable_shaded {
	background-color: #E8F0D0;
}
.skipsDataRowHide {
	display: none;
}

@media (max-width: 850px) { /* mobile */
	.skipsDefault { margin: 0; width: 100vw } /* calc(100vw); */
	#skipsApiTitle { font-size: 1em; }
	#skipsMapPanel {width: 100%; height: 350px; border-style: solid; border-color: gray; border-width: 2px 0; }
}
@media (min-width: 850px) {
	.skipsDefault { margin: 10px; }
	#skipsMapPanel {width: 820px; height: 410px; border: 6px solid gray; }
}
